                    {{ userData.persona.etnia }}
                  </b-col>
                </b-row>

                <b-row>
                  <b-col cols="5">
                    <span class="font-weight-bolder text-capitalize">celular</span>
                  </b-col>
                  <b-col cols="7">
                    {{ userData.persona.celular }}
                  </b-col>
                </b-row>

                <b-row>
                  <b-col cols="5">
                    <span class="font-weight-bolder text-capitalize">direccion</span>
                  </b-col>
                  <b-col cols="7">
                    {{ userData.persona.direccion }}
                  </b-col>
                </b-row>

              </b-card-body>
            </b-card>
          </b-col>

          <b-col
            lg="4"
            md="6"
            sm="12"
