            >
              <b-card-header class=" d-flex justify-content-center pt-50 pb-50">
                <b-card-title>

                  <b-badge variant="light-info">
                    DATOS DE LA ATENCIN
                  </b-badge>
                </b-card-title>
              </b-card-header>

              <b-card-body class="pb-0">
                <b-row>
