                >
                  <div
                    class="border rounded p-1 border-success"
                  >
                    <span>{{ userData.triaje.observaciones }}</span>
                  </div>
                </b-collapse>

              </b-card-body>
            </b-card>
          </b-col>

          <b-col
            lg="4"
            md="6"
            sm="12"
          >

            <b-card
              no-body
            >
              <b-card-header class="d-flex justify-content-center pt-50 pb-50">
