              </b-card-header>

              <b-card-body class="pb-0">

                <b-row>
                  <b-col cols="5">
                    <span class="font-weight-bolder text-capitalize">dni:</span>
                  </b-col>
                  <!-- <pre>{{ userData.usuario_dibu }}</pre> -->
