                </b-row>

              </b-card-body>
            </b-card>
          </b-col>

        </b-row>

      </b-card>

      <b-row class="match-height">

        <b-col
          v-if="userData.servicio.nombre === 'Odontologa'"
          cols="12"
        >
          <b-card
            class="text-center"
          >
            <b-row>
              <b-list-group horizontal="md">
                <b-list-group-item style="border-radius:0px;border: none; border-bottom: 3px solid green">
                  <strong class="text-primary">{{ odontogramaNombre }}</strong>
                </b-list-group-item>
                <b-list-group-item
