              </b-list-group>
            </b-row>
            <br>
            <user-odontograma
              :id-paciente="idPaciente"
              :paciente="userData.persona.apellido_paterno + ' ' + userData.persona.apellido_materno + ' ' + userData.persona.nombres"
            />

            <!--             <b-tabs>
                <b-tab
                  :title="odontogramaNombre"
                  active
                >
                  <b-card-text />
                </b-tab>
                <b-tab
                  title="Historia Odontograma"
                >
                  <b-card-text>
                    Historial de odontogramas
                    <historial-odontograma :id-paciente="idPaciente" />
                  </b-card-text>
                </b-tab>
              </b-tabs> -->
          </b-card>
        </b-col>

        <b-col lg="6">
          <atencion-diagnostico
            :diagnosticos="userData.diagnosticos"
          />
          <!-- <pre>{{ userData.diagnosticos }}</pre> -->
        </b-col>

        <b-col lg="6">
          <atencion-procedimiento
            :procedimientos="userData.procedimientos"
          />
        </b-col>

        <b-col lg="12">
          <b-card no-body>
