<template>
  <div>
    <b-modal
      id="modal_diagnostico"
      size="lg"
      title="Agregar diagnosticos"
      no-close-on-esc
      no-close-on-backdrop
      hide-header-close
      cancel-title="Cerrar"
      modal-class="modal-success"
    >
      <b-row>
        <b-col
          lg="10"
          md="12"
        >
          <label>Diagnostico CIE 10</label>
          <vue-autosuggest
            ref="autocomplete"
            v-model="query"
            :suggestions="suggestions"
            :input-props="inputProps"
            :section-configs="sectionConfigs"
            :render-suggestion="renderSuggestion"
            :get-suggestion-value="getSuggestionValue"
            @input="fetchResults"
          />

        </b-col>
        <b-col
          lg="2"
          md="12"
          class="mt-2"
        >
          <b-button
            class="mb-0"
            block
            variant="success"
            @click="actualizarDiagnosticos()"
          >
            Grabar
          </b-button>
        </b-col>
      </b-row>

      <b-table
        class="mt-1"
        :items="diagnosticos"
        :fields="fields"
        :busy.sync="isBusy"
        size="sm"
        sticky-header="250px"
        small
        head-variant="light"
        :empty-text="'No hay registros para mostrar'"
      >
        <template #cell(index)="row">
          {{ row.index +1 }}
        </template>
        <template #cell(actions)="row">
          <b-button
            size="sm"
            variant="outline-danger"
            @click="quitarDiagnostico(row.item)"
          >
            <feather-icon
              icon="Trash2Icon"
              size="12"
            />
          </b-button>
        </template>
      </b-table>
    </b-modal>
  </div>
</template>

<script>
/* eslint-disable vue/no-unused-components */
import {
  BButton, BTable, BRow, BCol, BAvatar,
} from 'bootstrap-vue'

import router from '@/router'
// import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import { VueAutosuggest } from 'vue-autosuggest'

export default {
  components: {
    BButton,
    BRow,
    BCol,
    BTable,
    VueAutosuggest,
    BAvatar,
  },
  data() {
    return {
      query: '',
      results: [],
      timeout: null,
      selected: null,
      debounceMilliseconds: 500,
      inputProps: {
        id: 'autosuggest__input_ajax',
        placeholder: 'Ingrese cdigo / descripcin.',
        class: 'form-control',
        name: 'ajax',
      },
      suggestions: [],
      sectionConfigs: {
        destinations: {
          limit: 50,
          label: 'Diagnsticos',
          onSelected: selected => {
            this.selected = selected.item
            this.diagnosticos.push(selected.item)
          },
        },
      },
      atencionId: router.currentRoute.params.id,
      diagnosticos: [],
      fields: [
        { key: 'index', label: 'N' },
        {
          key: 'codigo', label: 'CIE 10', sortable: true, class: 'text-left',
        },
        { key: 'descripcion' },
        { key: 'actions', label: 'Acciones' },
      ],
      isBusy: false,
    }
  },
  mounted() {
    this.$http.get(`atenciones/${this.atencionId}/diagnosticos`)
      .then(response => {
        this.diagnosticos = response.data.data
      })
  },
  methods: {
    quitarDiagnostico(item) {
      this.diagnosticos.splice(this.diagnosticos.indexOf(item), 1)
    },
    actualizarDiagnosticos() {
      const data = this.diagnosticos.map(item => item.id)
      this.$http.put(`atenciones/${this.atencionId}/diagnosticos`, { diagnosticos: data })
        .then(response => {
          this.selected = null
          document.getElementsByName('ajax')[0].value = null
          this.diagnosticos = response.data.data
          this.$emit('actualizar-diagnosticos-atencion', response.data.data)
        }).catch(error => {
          this.toastError(`${error.response.data.error}`)
        })
    },
    fetchResults() {
      const { query } = this
      if (query.length < 3) {
        return
      }
      const perPage = 50

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        const photosPromise = this.$http.get('diagnosticos', { params: { query, perPage } })
        photosPromise.then(response => {
          this.suggestions = []
          this.selected = null
          const users = response.data.data
          // eslint-disable-next-line no-unused-expressions
          users.length && this.suggestions.push({ name: 'destinations', data: users })
        })
      }, this.debounceMilliseconds)
    },
    renderSuggestion(suggestion) {
      // if (suggestion.name === 'hotels') {
      //   const image = suggestion.item
      //   return (
      //     <div class="d-flex">
      //       <div>
      //         <b-avatar src={image.thumbnailUrl} class="mr-50"></b-avatar>
      //       </div>
      //       <div>
      //         <span>{image.title}</span>
      //       </div>
      //     </div>
      //   )
      // }
      return (
          <div>
            <div class="text-danger font-weight-bold">
              {suggestion.item.codigo}
            </div>
            <div>
              <span>{suggestion.item.descripcion}</span>
            </div>
          </div>
      )
      // return suggestion.item.codigo + suggestion.item.descripcion
    },
    getSuggestionValue(suggestion) {
      const { codigo, item } = suggestion
      return codigo === 'hotels' ? item.title : `${item.codigo} ${item.descripcion}`
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
@import '@core/scss/vue/libs/vue-autosuggest.scss';
</style>
